@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav{
  font-family: 'Bebas Neue', cursive;
  font-weight: bold;
  font-size: 25px;

  line-height: 1em; 
  background-color: #F2C029;

}
.products{
  font-family: 'Montserrat', sans-serif;
}

.card_titles{
  font-family: 'Bebas Neue', cursive;
  font-weight: bold;
  
}

.titles_shop{
  font-family: 'Bebas Neue', cursive;
  font-weight: bold;
  font-size: 4rem;
  text-shadow: 5px 0px 2px #F2C029;
  letter-spacing: 0.5rem;
  
}

.brandtext{
  color: #176dde;
}
.image{

max-height: 15rem;
}


a{
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  font-family: 'Bebas Neue', cursive;
  
}
.conh1{
  -webkit-justify-content: center;
          justify-content: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: auto;
  background-color: #F2C029;
  border-radius: 10px;
  margin-right: auto;
}

.conh3{
  background-color: #260C07;
  border-radius: 10px;
  padding: 10px;
  
}

h1 {
  color: #260C07;
  text-shadow: 0px 3px 5px #F2C029;
  font-weight: bold;
  font-family: 'Bebas Neue', cursive;
  
  
  
}
h3{
  color: #F2C029;
  text-shadow: 2px 4px 10px #260C07;
  font-weight: bold;
  font-family: 'Montserrat', sans-serif;
  
  
}

.indexdesc {
  color: #F2C029;
  text-shadow: 0px 3px 2px #260C07;
  font-size: 30px;
  font-weight: italic;
  font-family: 'Bebas Neue', cursive;
}

.titulo {
  font-size: x-large;
}
.cocina {
  font-size: x-large;
}
.cont {
  text-align: center;
  text-shadow: 1px 3px 10px rgb(116, 116, 116);
  color: rgb(7, 0, 0);
}

.about {
  font-size: 20px;
}
.sobre {
  padding: 10px;
  border-radius: 10px;
  background-color: #F2C029 ;
  border-right: medium;
  border-color: #260C07;
  border-width: medium;
}
.title{
  font-family: 'Bebas Neue', cursive;
  font-weight: bolder;
}

